body, html {
  height: 87%;
  margin: 0; /* Add this line to remove default margin */
  padding: 0; /* Also remove default padding */
  background: #000;
  color: #fff;
}
.parallax {
  background-image: url('/assets/img/background.jpg');
  height: 100%; 
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
footer {
  text-align: center;
}
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
}

.blog-post {
  padding: 20px;
  border-radius: 5px;
}

.post-date {
}

.post-author {
  font-style: italic;
}

.post-content {
  margin-top: 10px;
}
.navbar {
  position: fixed;
  right: 0;
  left: 0;
  padding:20px;
  font-size:18pt;
  text-align:center;
  background: #000;
  color: #fff;
}
.navbar a {
  color: #fff;
}
a {
  color: #fff;
}
